package com.ragnorak.dev.ui.expanded

import androidx.compose.foundation.background
import androidx.compose.foundation.layout.Arrangement
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.Row
import androidx.compose.foundation.layout.fillMaxHeight
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.foundation.layout.fillMaxWidth
import androidx.compose.foundation.layout.padding
import androidx.compose.foundation.rememberScrollState
import androidx.compose.foundation.verticalScroll
import androidx.compose.material.Scaffold
import androidx.compose.runtime.Composable
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.graphics.Color
import com.ragnorak.dev.RanorakAppState
import com.ragnorak.dev.dimensions.Resources.dimension
import com.ragnorak.dev.ui.Content
import com.ragnorak.dev.ui.components.topGradient10
import com.ragnorak.dev.ui.header.HeaderComponent
import com.ragnorak.dev.ui.screens.menu.MenuScreen
import org.jetbrains.compose.resources.ExperimentalResourceApi

@OptIn(ExperimentalResourceApi::class)
@Composable
fun ExpandedMainContent(appState: RanorakAppState) {
    Scaffold(
        backgroundColor = Color.Transparent,
        scaffoldState = appState.scaffoldState,
    ) { innerPadding ->
        Row(
            Modifier
                .padding(innerPadding)
                .fillMaxSize(),
            horizontalArrangement = Arrangement.SpaceBetween,
            verticalAlignment = Alignment.Top
        ) {
            Column(
                modifier = Modifier
                    .fillMaxHeight()
                    .background(topGradient10)
                    .padding(vertical = dimension.paddingL, horizontal = dimension.paddingM)
                    .weight(1f)
                    .verticalScroll(rememberScrollState()),
                verticalArrangement = Arrangement.SpaceBetween
            ) {
                MenuScreen(appState)
            }

            Column(
                modifier = Modifier
                    .fillMaxWidth()
                    .weight(4f),
                horizontalAlignment = Alignment.Start
            ) {
                HeaderComponent()
                Column(
                    horizontalAlignment = Alignment.CenterHorizontally
                ) {
                    Content(appState)
                }
            }
        }
    }
}