package com.ragnorak.dev.ui.screens.projects

import androidx.compose.foundation.layout.Arrangement
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.Row
import androidx.compose.foundation.layout.Spacer
import androidx.compose.foundation.layout.fillMaxWidth
import androidx.compose.foundation.layout.padding
import androidx.compose.foundation.lazy.grid.GridCells
import androidx.compose.foundation.lazy.grid.LazyVerticalGrid
import androidx.compose.foundation.lazy.grid.items
import androidx.compose.material.Text
import androidx.compose.runtime.Composable
import androidx.compose.runtime.remember
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.platform.LocalUriHandler
import androidx.compose.ui.text.font.FontWeight
import androidx.compose.ui.unit.TextUnit
import androidx.compose.ui.unit.sp
import com.ragnorak.dev.color.Colors
import com.ragnorak.dev.dimensions.DimensionType
import com.ragnorak.dev.dimensions.Resources.dimension
import com.ragnorak.dev.dimensions.getLineHeight
import com.ragnorak.dev.strings.Resources.strings
import com.ragnorak.dev.ui.components.CardInfo
import com.ragnorak.dev.ui.components.Chip
import com.ragnorak.dev.ui.components.HoverButton
import com.ragnorak.dev.ui.components.NonlazyGrid
import com.ragnorak.dev.ui.components.SelectableText
import org.jetbrains.compose.resources.ExperimentalResourceApi
import org.jetbrains.compose.resources.vectorResource
import ragnorakdev.composeapp.generated.resources.Res
import ragnorakdev.composeapp.generated.resources.github_icon
import ragnorakdev.composeapp.generated.resources.play_store_icon

@OptIn(ExperimentalResourceApi::class)
@Composable
fun ProjectsScreen() {
    val viewModel = remember { ProjectsViewModel() }
    val uriHandler = LocalUriHandler.current
    val projectList = viewModel.getProjectList()

    Column(
        modifier = Modifier
            .padding(horizontal = dimension.paddingM),
        verticalArrangement = Arrangement.spacedBy(dimension.paddingM)
    ) {
        Text(
            text = strings.projects,
            fontSize = dimension.textSizeTitle,
            fontWeight = FontWeight.Bold
        )

        LazyVerticalGrid(
            columns = GridCells.Fixed(
                if (dimension.dimensionType == DimensionType.COMPACT) {
                    1
                } else {
                    2
                }
            ),
            verticalArrangement = Arrangement.spacedBy(dimension.paddingM),
            horizontalArrangement = Arrangement.spacedBy(dimension.paddingM),
        ) {
            items(projectList) { projectItem ->

                CardInfo(
                    verticalArrangement = Arrangement.SpaceBetween
                ) {
                    Column(
                        modifier = Modifier
                            .fillMaxWidth(),
                    ) {
                        SelectableText(projectItem.title, fontWeight = FontWeight.Bold)
                        Spacer(modifier = Modifier.padding(top = dimension.paddingS))
                        SelectableText(
                            projectItem.description,
                            lineHeight = getLineHeight(),
                        )
                        Spacer(modifier = Modifier.padding(top = dimension.paddingS))
                        projectItem.myContribution?.let {
                            SelectableText(
                                it,
                                lineHeight = getLineHeight(),
                            )
                        }
                        Spacer(modifier = Modifier.padding(top = dimension.paddingS))
                        NonlazyGrid(
                            columns = 3,
                            itemCount = projectItem.technologies.size,
                            seeMoreFeature = true
                        ) { itemIndex ->
                            Chip(
                                text = projectItem.technologies[itemIndex].name,
                                icon = projectItem.technologies[itemIndex].logo?.let { vectorResource(it) })
                        }

                    }
                    Row(
                        modifier = Modifier
                            .fillMaxWidth(),
                        horizontalArrangement = Arrangement.End,
                        verticalAlignment = Alignment.Bottom
                    )
                    {
                        HoverButton(
                            colorBackground = Colors.transparent10WhiteColor,
                            hoverColorBackground = Colors.transparent80WhiteColor,
                            label = strings.seeProject,
                            leadingIcon = if (projectItem.platform == PlatformType.PLAY_STORE) {
                                vectorResource(Res.drawable.play_store_icon)
                            } else {
                                vectorResource(Res.drawable.github_icon)
                            }
                        ) {
                            projectItem.projectUrl?.let { uriHandler.openUri(it) }
                        }
                    }
                }
            }
            item { Spacer(modifier = Modifier.padding(top = dimension.paddingS)) }
        }
    }
}