package com.ragnorak.dev.ui.components

import androidx.compose.foundation.clickable
import androidx.compose.foundation.interaction.MutableInteractionSource
import androidx.compose.foundation.interaction.collectIsHoveredAsState
import androidx.compose.foundation.layout.Arrangement
import androidx.compose.foundation.layout.Row
import androidx.compose.foundation.layout.size
import androidx.compose.material.Icon
import androidx.compose.material.Text
import androidx.compose.material3.AssistChipDefaults
import androidx.compose.runtime.Composable
import androidx.compose.runtime.getValue
import androidx.compose.runtime.remember
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.graphics.vector.ImageVector
import androidx.compose.ui.text.font.FontWeight
import com.ragnorak.dev.dimensions.Resources.dimension

@Composable
fun HoverButtonMenu(
    label: String,
    icon: ImageVector? = null,
    fontWeight: FontWeight = FontWeight.Normal,
    action: () -> Unit
) {

    val interactionSource = remember { MutableInteractionSource() }
    val isHovered by interactionSource.collectIsHoveredAsState()

    Row(horizontalArrangement = Arrangement.spacedBy(dimension.paddingS),
        verticalAlignment = Alignment.CenterVertically) {
        icon?.let {
            Icon(
                it,
                contentDescription = null,
                modifier = Modifier.size(dimension.iconSize),
                tint = if (isHovered) Color.Gray else Color.Black,
            )
        }

        Text(
            modifier = Modifier
                .clickable(
                    indication = null,
                    interactionSource = interactionSource,
                    onClick = { action() }),
            text = label,
            fontSize = dimension.textSize,
            color = if (isHovered) Color.Gray else Color.Black,
            fontWeight = fontWeight
        )
    }
}