package com.ragnorak.dev.ui.screens.skills

import org.jetbrains.compose.resources.DrawableResource
import org.jetbrains.compose.resources.ExperimentalResourceApi
import org.jetbrains.skia.Drawable

data class SkillModel(
    val skillTitle: String,
    val logo: Drawable? = null,
    val frameworkList: List<FrameworkSkillModel>? = null
)

@OptIn(ExperimentalResourceApi::class)
data class FrameworkSkillModel(
    val name: String,
    val logo: DrawableResource? = null,
    val experienceTime:String? = null
)