package com.ragnorak.dev.ui.components

import androidx.compose.foundation.BorderStroke
import androidx.compose.foundation.interaction.MutableInteractionSource
import androidx.compose.foundation.interaction.collectIsHoveredAsState
import androidx.compose.foundation.layout.padding
import androidx.compose.foundation.layout.size
import androidx.compose.foundation.shape.RoundedCornerShape
import androidx.compose.material.Button
import androidx.compose.material.ButtonDefaults
import androidx.compose.material.Icon
import androidx.compose.material.Text
import androidx.compose.material3.AssistChipDefaults
import androidx.compose.runtime.Composable
import androidx.compose.runtime.getValue
import androidx.compose.runtime.remember
import androidx.compose.ui.Modifier
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.graphics.vector.ImageVector
import androidx.compose.ui.text.style.TextAlign
import androidx.compose.ui.unit.dp
import com.ragnorak.dev.dimensions.Resources.dimension

@Composable
fun HoverButton(
    label: String,
    colorBackground: Color = Color.Black,
    hoverColorBackground: Color = Color.White,
    colorText: Color = Color.Black,
    hoverColorText: Color = Color.Black,
    leadingIcon: ImageVector? = null,
    action: () -> Unit
) {

    val interactionSource = remember { MutableInteractionSource() }
    val isHovered by interactionSource.collectIsHoveredAsState()

    Button(
        elevation = ButtonDefaults.elevation(
            defaultElevation = 0.dp,
            pressedElevation = 0.dp,
            disabledElevation = 0.dp,
            hoveredElevation = 0.dp),
        interactionSource = interactionSource,
        modifier = Modifier
            .padding(dimension.paddingM),
        border = BorderStroke(
            width = 2.dp,
            color = if (isHovered) hoverColorText else colorText
        ),
        shape = RoundedCornerShape(dimension.paddingM),
        onClick = action,
        colors = ButtonDefaults.outlinedButtonColors(
            backgroundColor = if (isHovered) hoverColorBackground else colorBackground),
    ) {
        leadingIcon?.let {
            Icon(
                it,
                contentDescription = null,
                modifier = Modifier.size(dimension.iconSize),
                tint = Color.Unspecified,
            )
        }
        Text(modifier = Modifier
            .padding(start = dimension.paddingS),
            textAlign = TextAlign.Center,
            fontSize = dimension.textSize,
            text = label,
            color = if (isHovered) hoverColorText else colorText
        )
    }
}