package com.ragnorak.dev

import androidx.compose.foundation.background
import androidx.compose.foundation.layout.Box
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.material.MaterialTheme
import androidx.compose.runtime.Composable
import androidx.compose.runtime.CompositionLocalProvider
import androidx.compose.ui.Modifier
import androidx.compose.ui.draw.paint
import androidx.compose.ui.layout.ContentScale
import com.ragnorak.dev.dimensions.DimensionFactory
import com.ragnorak.dev.dimensions.DimensionResources
import com.ragnorak.dev.dimensions.DimensionType
import com.ragnorak.dev.dimensions.LocalDimensionResources
import com.ragnorak.dev.dimensions.Resources.dimension
import com.ragnorak.dev.strings.LocalStringResources
import com.ragnorak.dev.strings.StringFactory
import com.ragnorak.dev.strings.StringResources
import com.ragnorak.dev.ui.compact.CompactMainContent
import com.ragnorak.dev.ui.components.inMiddleGradient
import com.ragnorak.dev.ui.expanded.ExpandedMainContent
import org.jetbrains.compose.resources.ExperimentalResourceApi
import org.jetbrains.compose.resources.painterResource
import ragnorakdev.composeapp.generated.resources.Res
import ragnorakdev.composeapp.generated.resources.ragnorak_background

@OptIn(ExperimentalResourceApi::class)
@Composable
fun App(
    stringResources: StringResources = StringFactory.createStrings(language),
    dimensionResources: DimensionResources = DimensionFactory.createDimensions(density)
) {
    CompositionLocalProvider(
        LocalStringResources provides stringResources,
        LocalDimensionResources provides dimensionResources
    ) {
        MaterialTheme {
            val appState = rememberRanorakAppState()
            Box(
                modifier = Modifier
                    .fillMaxSize()
                    .paint(
                        painter = painterResource(Res.drawable.ragnorak_background),
                        contentScale = ContentScale.Crop
                    )
                    .background(inMiddleGradient)
            ) {
                if (dimension.dimensionType == DimensionType.COMPACT) {
                    CompactMainContent(appState)
                } else {
                    ExpandedMainContent(appState)
                }
            }
        }
    }
}