package com.ragnorak.dev.ui.components

import androidx.compose.foundation.layout.Arrangement
import androidx.compose.foundation.layout.Box
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.Row
import androidx.compose.runtime.Composable
import androidx.compose.runtime.getValue
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.remember
import androidx.compose.runtime.setValue
import androidx.compose.ui.Modifier
import androidx.compose.ui.text.font.FontWeight
import com.ragnorak.dev.dimensions.Resources.dimension
import com.ragnorak.dev.strings.Resources.strings

@Composable
fun NonlazyGrid(
    columns: Int,
    itemCount: Int,
    modifier: Modifier = Modifier,
    seeMoreFeature: Boolean = false,
    content: @Composable (Int) -> Unit
) {

    var seeMoreState by remember { mutableStateOf(false) }

    Column(modifier = modifier) {
        var rows = 1

        if (seeMoreState) {
            rows = (itemCount / columns)

            if (itemCount.mod(columns) > 0) {
                rows += 1
            }
        }

        for (rowId in 0 until rows) {
            val firstIndex = rowId * columns

            Row(horizontalArrangement = Arrangement.spacedBy(dimension.paddingS)) {
                for (columnId in 0 until columns) {
                    val index = firstIndex + columnId
                    Box(
                        modifier = Modifier
                    ) {
                        if (index < itemCount) {
                            content(index)
                        }
                    }
                }
            }
        }
        if(seeMoreFeature && itemCount > columns) {
            HoverButtonMenu(
                label = if (seeMoreState) { strings.seeLess } else { strings.seeMore },
                fontWeight = FontWeight.ExtraLight
            ) { seeMoreState = !seeMoreState }
        }
    }
}