@OptIn(ExperimentalResourceApi::class, ExperimentalComposeUiApi::class)
@Composable
fun MenuScreen(appState: RanorakAppState) {
    Column {
        Column(
            modifier = Modifier.fillMaxWidth(),
            horizontalAlignment = Alignment.CenterHorizontally
        ) {
            Image(
                alignment = Alignment.TopCenter,
                painter = painterResource(Res.drawable.ragnorak_background_juntos_2_colores),
                contentDescription = null,
                contentScale = ContentScale.Crop,
                modifier = Modifier
                    .size(dimension.logoImageSize)
                    .clip(CircleShape)
                    .border(2.dp, Color.Gray, CircleShape)
            )
        }

        Spacer(modifier = Modifier.padding(dimension.paddingM))
        ContactView()
        Spacer(modifier = Modifier.padding(dimension.paddingL))
        Column(
            verticalArrangement = Arrangement.spacedBy(dimension.paddingS)
        ) {
            HoverButtonMenu(
                label = Resources.strings.start,
                icon = vectorResource(Res.drawable.home_icon)
            ) { appState.navigateToRoute(DestinationMenu.START.route) }
            HoverButtonMenu(
                label = Resources.strings.aboutMe,
                icon = vectorResource(Res.drawable.aboutme_icon)
            ) { appState.navigateToRoute(DestinationMenu.ABOUT_ME.route) }
            HoverButtonMenu(
                label = Resources.strings.projects,
                icon = vectorResource(Res.drawable.computer_programmer_icon)
            ) { appState.navigateToRoute(DestinationMenu.PROJECTS.route) }
            HoverButtonMenu(
                label = Resources.strings.skills,
                icon = vectorResource(Res.drawable.skills_icon)
            ) { appState.navigateToRoute(DestinationMenu.SKILLS.route) }
            HoverButtonMenu(
                label = Resources.strings.laborExperiences,
                icon = vectorResource(Res.drawable.labor_experience_icon)
            ) { appState.navigateToRoute(DestinationMenu.LABOR_EXPERIENCE.route) }
            HoverButtonMenu(
                label = Resources.strings.articles,
                icon = vectorResource(Res.drawable.articles_icon)
            ) { appState.navigateToRoute(DestinationMenu.ARTICLES.route) }
            HoverButtonMenu(
                label = Resources.strings.education,
                icon = vectorResource(Res.drawable.education_icon)
            ) { appState.navigateToRoute(DestinationMenu.EDUCATION.route) }
        }
    }

    Column(
        verticalArrangement = Arrangement.Bottom
    ) {
       Text(Resources.strings.powerTech)
    }
}

@OptIn(ExperimentalResourceApi::class)
@Composable
fun CompactMenu(appState: RanorakAppState) {
    var selectedItem by remember { mutableStateOf(DestinationMenu.START.route) }
    val menuItems = listOf(
        BottomNavigationItem(
            label = Resources.strings.start,
            icon = vectorResource(Res.drawable.home_icon),
            route = DestinationMenu.START.route
        ),
        BottomNavigationItem(
            label = Resources.strings.aboutMe,
            icon = vectorResource(Res.drawable.aboutme_icon),
            route = DestinationMenu.ABOUT_ME.route
        ),
        BottomNavigationItem(
            label = Resources.strings.projects,
            icon = vectorResource(Res.drawable.computer_programmer_icon),
            route = DestinationMenu.PROJECTS.route
        ),
        BottomNavigationItem(
            label = Resources.strings.skills,
            icon = vectorResource(Res.drawable.skills_icon),
            route = DestinationMenu.SKILLS.route
        ),
        BottomNavigationItem(
            label = Resources.strings.laborExperiences,
            icon = vectorResource(Res.drawable.labor_experience_icon),
            route = DestinationMenu.LABOR_EXPERIENCE.route
        ),
        BottomNavigationItem(
            label = Resources.strings.articles,
            icon = vectorResource(Res.drawable.articles_icon),
            route = DestinationMenu.ARTICLES.route
        ),
        BottomNavigationItem(
            label = Resources.strings.education,
            icon = vectorResource(Res.drawable.education_icon),
            route = DestinationMenu.EDUCATION.route
        )
    )
    NavigationBar(modifier = Modifier.padding(horizontal = dimension.paddingS),
        containerColor = Colors.transparent10WhiteColor,
        contentColor = Colors.transparent10WhiteColor
    ) {
        menuItems.forEach {
            NavigationBarItem(
                modifier = Modifier.padding(vertical = dimension.paddingS),
                colors = NavigationBarItemDefaults.colors(
                    indicatorColor = Colors.transparent80WhiteColor
                ),
                icon = {
                    Icon(
                        imageVector = it.icon,
                        contentDescription = null,
                        modifier = Modifier.size(80.dp)
                    )
                },
                label = {
                   Text(
                        text = it.label,
                        textAlign = TextAlign.Center,
                        fontSize = dimension.textSizeMenu,
                        maxLines = 2,
                        minLines = 2,
                    )
                },
                selected = selectedItem == it.route,
                onClick = {
                    selectedItem = it.route
                    appState.navigateToRoute(it.route)
                }
            )
        }

    }
}