package com.ragnorak.dev.ui.screens.education

import androidx.compose.foundation.layout.Arrangement
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.Spacer
import androidx.compose.foundation.layout.fillMaxWidth
import androidx.compose.foundation.layout.padding
import androidx.compose.foundation.lazy.grid.GridCells
import androidx.compose.foundation.lazy.grid.LazyVerticalGrid
import androidx.compose.foundation.lazy.grid.items
import androidx.compose.material.Text
import androidx.compose.runtime.Composable
import androidx.compose.runtime.remember
import androidx.compose.ui.Modifier
import androidx.compose.ui.text.font.FontWeight
import com.ragnorak.dev.dimensions.DimensionType
import com.ragnorak.dev.dimensions.Resources.dimension
import com.ragnorak.dev.dimensions.getLineHeight
import com.ragnorak.dev.strings.Resources.strings
import com.ragnorak.dev.ui.components.CardInfo
import com.ragnorak.dev.ui.components.ColumnBorder
import com.ragnorak.dev.ui.components.SelectableText

@Composable
fun EducationScreen() {
    val viewModel = remember { EducationViewModel() }
    val educationList = viewModel.getEducationList()
    Column(modifier = Modifier
        .padding(horizontal = dimension.paddingM),
        verticalArrangement = Arrangement.spacedBy(dimension.paddingM)
    ) {
        Text(
            text = strings.education,
            fontSize = dimension.textSizeTitle,
            fontWeight = FontWeight.Bold
        )

        LazyVerticalGrid(
            columns = GridCells.Fixed(
                if (dimension.dimensionType == DimensionType.COMPACT) {
                    1
                } else {
                    2
                }
            ),
            verticalArrangement = Arrangement.spacedBy(dimension.paddingM),
            horizontalArrangement = Arrangement.spacedBy(dimension.paddingM),
        ) {

            items(educationList) { educationInfo ->
                CardInfo(
                    verticalArrangement = Arrangement.SpaceBetween,
                    heightMin = dimension.cardSmallHeight
                ) {
                    Column(
                        modifier = Modifier
                            .fillMaxWidth(),
                    ) {
                        SelectableText(educationInfo.name, fontWeight = FontWeight.Bold)
                        Spacer(modifier = Modifier.padding(top = dimension.paddingM))
                        educationInfo.place?.let {
                            SelectableText(it)
                        }
                        educationInfo.date?.let {
                            SelectableText(it)
                        }
                        Spacer(modifier = Modifier.padding(top = dimension.paddingM))
                        educationInfo.descriptionProject?.let {
                            SelectableText(it, lineHeight = getLineHeight())
                        }
                    }
                }
            }
            item { Spacer(modifier = Modifier.padding(top = dimension.paddingS)) }
        }
    }
}