package com.ragnorak.dev.ui.screens.start

import androidx.compose.foundation.layout.Arrangement
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.Row
import androidx.compose.foundation.layout.Spacer
import androidx.compose.foundation.layout.fillMaxWidth
import androidx.compose.foundation.layout.padding
import androidx.compose.foundation.layout.size
import androidx.compose.foundation.lazy.grid.GridCells
import androidx.compose.foundation.lazy.grid.LazyVerticalGrid
import androidx.compose.material.Icon
import androidx.compose.material3.AssistChipDefaults
import androidx.compose.runtime.Composable
import androidx.compose.runtime.remember
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.text.font.FontWeight
import com.ragnorak.dev.RanorakAppState
import com.ragnorak.dev.color.Colors
import com.ragnorak.dev.dimensions.DimensionType
import com.ragnorak.dev.dimensions.Resources.dimension
import com.ragnorak.dev.strings.Resources.strings
import com.ragnorak.dev.ui.DestinationMenu
import com.ragnorak.dev.ui.components.CardInfo
import com.ragnorak.dev.ui.components.HoverButton
import com.ragnorak.dev.ui.components.SelectableText
import org.jetbrains.compose.resources.ExperimentalResourceApi
import org.jetbrains.compose.resources.vectorResource

@OptIn(ExperimentalResourceApi::class)
@Composable
fun StartScreen(appState: RanorakAppState) {
    val viewModel = remember { StartViewModel() }
    val startContentInfo = viewModel.getStartContent()
    LazyVerticalGrid(
        modifier = Modifier.padding(horizontal = dimension.paddingM),
        columns = GridCells.Fixed(if (dimension.dimensionType == DimensionType.COMPACT) {1} else {2}),
        horizontalArrangement = Arrangement.spacedBy(dimension.paddingM),
        verticalArrangement = Arrangement.spacedBy(dimension.paddingM)
    ) {
        item {
            CardInfo(
                horizontalAlignment = Alignment.Start,
                verticalArrangement = Arrangement.SpaceBetween,
                heightMin = dimension.cardSmallHeight
            ) {
                SelectableText(text = strings.mainSkills, fontWeight = FontWeight.Bold)
                Spacer(modifier = Modifier.padding(top = dimension.paddingS))
                Column(
                    modifier = Modifier
                        .fillMaxWidth(),
                    horizontalAlignment = Alignment.Start
                ) {
                    startContentInfo.mainSkills.forEach {
                        Row {
                            it.logo?.let {
                                Icon(
                                    vectorResource(it),
                                    contentDescription = null,
                                    modifier = Modifier.size(dimension.iconSize),
                                    tint = Color.Unspecified,
                                )
                            } ?: Spacer(modifier = Modifier.padding(start = dimension.iconSize))

                            SelectableText(
                                text = it.name,
                                modifier = Modifier.padding(horizontal = dimension.paddingM)
                            )
                        }
                    }
                    Row(
                        modifier = Modifier
                            .fillMaxWidth(),
                        horizontalArrangement = Arrangement.End,
                        verticalAlignment = Alignment.Bottom
                    )
                    {
                        HoverButton(
                            colorBackground = Colors.transparent10WhiteColor,
                            hoverColorBackground = Colors.transparent80WhiteColor,
                            label = strings.seeMore,
                        ) {
                            appState.navigateToRoute(DestinationMenu.SKILLS.route)
                        }
                    }
                }
            }
        }
        item {
            CardInfo(
                horizontalAlignment = Alignment.Start,
                verticalArrangement = Arrangement.SpaceBetween,
                heightMin = dimension.cardSmallHeight
            ) {
                SelectableText(text = strings.learningSkills, fontWeight = FontWeight.Bold)
                Spacer(modifier = Modifier.padding(top = dimension.paddingS))
                Column(
                    modifier = Modifier
                        .fillMaxWidth(),
                    horizontalAlignment = Alignment.Start
                ) {
                    startContentInfo.learningSkills.forEach {
                        Row {
                            it.logo?.let {
                                Icon(
                                    vectorResource(it),
                                    contentDescription = null,
                                    modifier = Modifier.size(dimension.iconSize),
                                    tint = Color.Unspecified,
                                )
                            } ?: Spacer(modifier = Modifier.padding(start = dimension.iconSize))

                            SelectableText(
                                text = it.name,
                                modifier = Modifier.padding(horizontal = dimension.paddingM)
                            )
                        }
                    }
                }
                Row(
                    modifier = Modifier
                        .fillMaxWidth(),
                    horizontalArrangement = Arrangement.End,
                    verticalAlignment = Alignment.Bottom
                )
                {
                    HoverButton(
                        colorBackground = Colors.transparent10WhiteColor,
                        hoverColorBackground = Colors.transparent80WhiteColor,
                        label = strings.seeMore,
                    ) {
                        appState.navigateToRoute(DestinationMenu.SKILLS.route)
                    }
                }
            }
        }
        item(startContentInfo.articles) {
            CardInfo(
                horizontalAlignment = Alignment.Start,
                heightMin = dimension.cardSmallHeight
            ) {
                SelectableText(text = strings.articles, fontWeight = FontWeight.Bold)
                Spacer(modifier = Modifier.padding(top = dimension.paddingS))
                Column(
                    modifier = Modifier
                        .fillMaxWidth(),
                    horizontalAlignment = Alignment.Start
                ) {
                    startContentInfo.articles.forEach {
                        Row {
                            SelectableText(
                                text = it.title,
                                modifier = Modifier.padding(horizontal = dimension.paddingM)
                            )
                        }
                    }
                }
                Row(
                    modifier = Modifier
                        .fillMaxWidth(),
                    horizontalArrangement = Arrangement.End,
                    verticalAlignment = Alignment.Bottom
                )
                {
                    HoverButton(
                        colorBackground = Colors.transparent10WhiteColor,
                        hoverColorBackground = Colors.transparent80WhiteColor,
                        label = strings.seeMore,
                    ) {
                        appState.navigateToRoute(DestinationMenu.ARTICLES.route)
                    }
                }
            }
        }
        item(startContentInfo.mainProjects) {
            CardInfo(
                horizontalAlignment = Alignment.Start,
                heightMin = dimension.cardSmallHeight
            ) {
                SelectableText(text = strings.mainProjects, fontWeight = FontWeight.Bold)
                Spacer(modifier = Modifier.padding(top = dimension.paddingS))
                Column(
                    modifier = Modifier
                        .fillMaxWidth(),
                    horizontalAlignment = Alignment.Start
                ) {
                    startContentInfo.mainProjects.forEach {
                        SelectableText(
                            text = it.title,
                            modifier = Modifier.padding(horizontal = dimension.paddingM)
                        )
                    }
                }
                Row(
                    modifier = Modifier
                        .fillMaxWidth(),
                    horizontalArrangement = Arrangement.End,
                    verticalAlignment = Alignment.Bottom
                )
                {
                    HoverButton(
                        colorBackground = Colors.transparent10WhiteColor,
                        hoverColorBackground = Colors.transparent80WhiteColor,
                        label = strings.seeMore,
                    ) {
                        appState.navigateToRoute(DestinationMenu.PROJECTS.route)
                    }
                }
            }
        }
        item {  Spacer(modifier = Modifier.padding(top = dimension.paddingS)) }
    }
}