package com.ragnorak.dev.ui.header

import androidx.compose.foundation.layout.Arrangement
import androidx.compose.foundation.layout.Box
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.Row
import androidx.compose.foundation.layout.fillMaxWidth
import androidx.compose.foundation.layout.padding
import androidx.compose.foundation.layout.size
import androidx.compose.material.Icon
import androidx.compose.material.Text
import androidx.compose.runtime.Composable
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.text.font.FontWeight
import com.ragnorak.dev.dimensions.DimensionType
import com.ragnorak.dev.dimensions.Resources.dimension
import com.ragnorak.dev.strings.Resources.strings
import com.ragnorak.dev.ui.components.SelectableText
import com.ragnorak.dev.ui.screens.menu.CompactContactView
import org.jetbrains.compose.resources.ExperimentalResourceApi
import org.jetbrains.compose.resources.painterResource
import ragnorakdev.composeapp.generated.resources.Res
import ragnorakdev.composeapp.generated.resources.android_logo

@OptIn(ExperimentalResourceApi::class)
@Composable
fun HeaderComponent() {
    Box {
        Column(
            modifier = Modifier
                .padding(dimension.paddingM)
                .align(Alignment.TopStart)
        ) {
            if (dimension.dimensionType == DimensionType.COMPACT) {
                SelectableText(strings.powerTech, fontSize = dimension.textSmallSize)
            }
            Row(
                modifier = Modifier.fillMaxWidth(),
                verticalAlignment = Alignment.CenterVertically,
                horizontalArrangement = Arrangement.SpaceBetween
            ) {
                Row(verticalAlignment = Alignment.CenterVertically) {
                    Text(
                        text = strings.greet,
                        fontSize = dimension.textSizeHeaderTitle,
                        fontWeight = FontWeight.ExtraLight
                    )
                    Icon(
                        painterResource(Res.drawable.android_logo),
                        contentDescription = null,
                        modifier = Modifier.size(dimension.logoImageHeaderSize),
                        tint = Color.Unspecified,
                    )
                }
            }
            Row(
                verticalAlignment = Alignment.Bottom,
                horizontalArrangement = Arrangement.spacedBy(dimension.paddingS)
            ) {
                Text(
                    text = strings.iAm,
                    fontSize = dimension.textSizeHeaderName,
                    fontWeight = FontWeight.ExtraLight
                )
                Text(
                    text = strings.myName,
                    fontSize = dimension.textSizeHeaderName,
                    fontWeight = FontWeight.Bold
                )
            }

            Text(
                text = strings.myDescription,
                fontSize = dimension.textSizeHeaderDescription,
                fontWeight = FontWeight.ExtraLight
            )
        }
        if (dimension.dimensionType == DimensionType.COMPACT) {
            CompactContactView(modifier = Modifier
                .padding(dimension.paddingM)
                .align(Alignment.TopEnd))
        }
    }
}