package com.ragnorak.dev.dimensions

import androidx.compose.runtime.Composable
import androidx.compose.ui.unit.Dp
import androidx.compose.ui.unit.TextUnit
import androidx.compose.ui.unit.sp
import com.ragnorak.dev.dimensions.Resources.dimension

interface DimensionResources {
    val dimensionType: DimensionType
    val contentHeight: Dp
    val menuHeight: Dp
    val buttonHeight: Dp
    val buttonWidth: Dp
    val cardHeight: Dp
    val cardSmallHeight: Dp
    val cardWidth: Dp
    val textSmallSize: TextUnit
    val textSize: TextUnit
    val textSizeTitle: TextUnit
    val textSizeMenu: TextUnit
    val textSizeHeaderTitle: TextUnit
    val textSizeHeaderName: TextUnit
    val textSizeHeaderDescription: TextUnit
    val logoImageHeaderSize: Dp
    val logoImageSize: Dp
    val iconSize: Dp
    val iconFlagSize: Dp

    val paddingS: Dp
    val paddingM: Dp
    val paddingL: Dp
    val paddingXL: Dp
}

@Composable
 fun getLineHeight(): TextUnit = if (dimension.dimensionType == DimensionType.COMPACT) {
    40.sp
} else {
    25.sp
}