@OptIn(ExperimentalResourceApi::class)
@Composable
fun ContactView() {
    val uriHandler = LocalUriHandler.current
    Column(
        verticalArrangement = Arrangement.spacedBy(dimension.paddingS)
    ) {
        LanguageOptionsView()
        TextCopyable("raulillangarcia@gmail.com")
        TextCopyable("ragnorakdev@gmail.com")

        HoverButtonMenu(
            label = Resources.strings.linkedInLabel,
            icon = vectorResource(Res.drawable.linkedin_icon)
        ) {
            uriHandler.openUri("https://www.linkedin.com/in/raul-illan-garcia-42452b55")
        }
        HoverButtonMenu(
            label = Resources.strings.gihubLabel,
            icon = vectorResource(Res.drawable.github_icon)
        ) {
            uriHandler.openUri("https://github.com/ragnorak-dev")
        }
    }
}

@OptIn(ExperimentalResourceApi::class)
@Composable
fun CompactContactView(modifier: Modifier = Modifier) {
    val uriHandler = LocalUriHandler.current
    Column(
        modifier = modifier,
        horizontalAlignment = Alignment.End,
        verticalArrangement = Arrangement.spacedBy(dimension.paddingS)
    ) {

        TextCopyable("raulillangarcia@gmail.com")
        TextCopyable("ragnorakdev@gmail.com")

        HoverButtonMenu(
            label = Resources.strings.linkedInLabel,
            icon = vectorResource(Res.drawable.linkedin_icon)
        ) {
            uriHandler.openUri("https://www.linkedin.com/in/raul-illan-garcia-42452b55")
        }
        HoverButtonMenu(
            label = Resources.strings.gihubLabel,
            icon = vectorResource(Res.drawable.github_icon)
        ) {
            uriHandler.openUri("https://github.com/ragnorak-dev")
        }
        Spacer(modifier = Modifier.padding(vertical = dimension.paddingS))
        LanguageOptionsView()
    }
}

@OptIn(ExperimentalResourceApi::class)
@Composable
private fun LanguageOptionsView() {
    Row(
        horizontalArrangement = Arrangement.spacedBy(dimension.paddingS)
    ) {
        Icon(
            imageVector = vectorResource(Res.drawable.spain_flag_icon),
            contentDescription = null,
            tint = Color.Unspecified,
            modifier = Modifier.size(dimension.iconFlagSize)
                .clickable {
                    language.value = ES_LANG
                }
        )
        Icon(
            imageVector = vectorResource(Res.drawable.uk_flag_icon),
            contentDescription = null,
            tint = Color.Unspecified,
            modifier = Modifier.size(dimension.iconFlagSize)
                .clickable {
                    language.value = EN_LANG
                }
        )
    }
}