package com.ragnorak.dev.ui.components

import androidx.compose.foundation.background
import androidx.compose.foundation.layout.Arrangement
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.height
import androidx.compose.foundation.layout.heightIn
import androidx.compose.foundation.layout.padding
import androidx.compose.foundation.layout.width
import androidx.compose.foundation.layout.widthIn
import androidx.compose.foundation.shape.RoundedCornerShape
import androidx.compose.runtime.Composable
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.draw.clip
import androidx.compose.ui.unit.Dp
import androidx.compose.ui.unit.dp
import com.ragnorak.dev.dimensions.Resources.dimension

@Composable
fun CardInfo(
    modifier: Modifier = Modifier,
    horizontalAlignment: Alignment.Horizontal = Alignment.CenterHorizontally,
    verticalArrangement: Arrangement.Vertical = Arrangement.Center,
    heightMin: Dp = dimension.cardHeight,
    content: @Composable () -> Unit
) {
    Column(
        modifier = modifier
            .heightIn(min = heightMin)
            .widthIn(min = dimension.cardWidth)
            .clip(RoundedCornerShape(10.dp))
            .background(topGradient10)
            .padding(horizontal = dimension.paddingM, vertical = dimension.paddingL),
        horizontalAlignment = horizontalAlignment,
        verticalArrangement = verticalArrangement
    ) {
        content()
    }
}