package com.ragnorak.dev.ui

import androidx.compose.foundation.layout.Box
import androidx.compose.runtime.Composable
import androidx.compose.runtime.MutableState
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.remember
import com.ragnorak.dev.RanorakAppState
import com.ragnorak.dev.ui.components.AnimationScreen
import com.ragnorak.dev.ui.screens.aboutMe.AboutMeScreen
import com.ragnorak.dev.ui.screens.articles.ArticlesScreen
import com.ragnorak.dev.ui.screens.education.EducationScreen
import com.ragnorak.dev.ui.screens.laborExperience.LaborExperienceScreen
import com.ragnorak.dev.ui.screens.projects.ProjectsScreen
import com.ragnorak.dev.ui.screens.skills.SkillsScreen
import com.ragnorak.dev.ui.screens.start.StartScreen

@Composable
fun Content(appState: RanorakAppState) {

    val destinationMap: HashMap<String, MutableState<Boolean>> = remember {
        hashMapOf(
            Pair(DestinationMenu.START.route, mutableStateOf(false)),
            Pair(DestinationMenu.ABOUT_ME.route, mutableStateOf(false)),
            Pair(DestinationMenu.PROJECTS.route, mutableStateOf(false)),
            Pair(DestinationMenu.SKILLS.route, mutableStateOf(false)),
            Pair(DestinationMenu.LABOR_EXPERIENCE.route, mutableStateOf(false)),
            Pair(DestinationMenu.ARTICLES.route, mutableStateOf(false)),
            Pair(DestinationMenu.EDUCATION.route, mutableStateOf(false)),
        )
    }
    destinationMap.map { it.value.value = false }
    destinationMap[appState.currentRoute]?.value = true
    Box {
        AnimationScreen(destinationMap[DestinationMenu.START.route]?.value ?: false) {
            StartScreen(appState)
        }
        AnimationScreen(destinationMap[DestinationMenu.ABOUT_ME.route]?.value ?: false) {
            AboutMeScreen()
        }
        AnimationScreen(destinationMap[DestinationMenu.PROJECTS.route]?.value ?: false) {
            ProjectsScreen()
        }
        AnimationScreen(destinationMap[DestinationMenu.SKILLS.route]?.value ?: false) {
            SkillsScreen()
        }
        AnimationScreen(destinationMap[DestinationMenu.LABOR_EXPERIENCE.route]?.value ?: false) {
            LaborExperienceScreen()
        }
        AnimationScreen(destinationMap[DestinationMenu.ARTICLES.route]?.value ?: false) {
            ArticlesScreen()
        }
        AnimationScreen(destinationMap[DestinationMenu.EDUCATION.route]?.value ?: false) {
            EducationScreen()
        }
    }
}